import React from 'react';
import MaterialTable from 'material-table';

export default function Incidents({data}) {
    const [state, setState] = React.useState({
        columns: [
            { title: 'Number', field: 'number' },
            { title: 'Opened', field: 'opened_at' },
            { title: 'Short Description', field: 'short_description'},
            
        ],
        data: [
            
        ],
    });

    console.log(data);

    return (
        <MaterialTable
            style={{ margin: "16px" }}
            title="Incidents"
            columns={state.columns}
            data={data?data:state.data}
            editable={{
                onRowAdd: newData =>
                    new Promise(resolve => {
                        setTimeout(() => {
                            resolve();
                            const data = [...state.data];
                            data.push(newData);
                            setState({ ...state, data });
                        }, 600);
                    }),
                onRowUpdate: (newData, oldData) =>
                    new Promise(resolve => {
                        setTimeout(() => {
                            resolve();
                            const data = [...state.data];
                            data[data.indexOf(oldData)] = newData;
                            setState({ ...state, data });
                        }, 600);
                    }),
                onRowDelete: oldData =>
                    new Promise(resolve => {
                        setTimeout(() => {
                            resolve();
                            const data = [...state.data];
                            data.splice(data.indexOf(oldData), 1);
                            setState({ ...state, data });
                        }, 600);
                    }),
            }}
        />
    );
}
